import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";

export const Completed = () => {
    const auth = useSelector(state => state.userStatus.auth)
    const history = useHistory();
    useEffect(() => {
        if (!auth) history.push("/");
        return () => {
            //
        };
    }, [auth]);
    return (
        <div class="container-fluid">
            <div class="InProgressCourses">
                <div class="center">
                    <div class="Inprogress">
                        <section>
                            <header>
                                <div class="CourseHeader">
                                    <div class="header-content">Công nghệ thông tin</div>
                                </div>
                            </header>
                            <div class="course-main-content">
                                <div class="course-main-content-center row">
                                    <div class="course-main-content-primary col-md-6 col-12">
                                        <a>
                                            <h1 class="course-main-content-title">
                                                Khóa học dạy lập trình từ cơ bản đến nâng cao: HTML, CSS, Bootstrap
                                            </h1>
                                        </a>
                                        <span class="course-pill">Đã hoàn thành</span>
                                    </div>
                                    <div class="course-main-content-secondary col-md-6 col-12">
                                        <div class="course-main-content-button">
                                            <div class="Course-link-button">
                                                <a href="Thongtinkhoahoc.html">
                                                    <span>Đến khóa học</span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <footer class="Course-footer">
                                <div class="partner-name">
                                    <div> Đại học CNTT và Truyền thông Việt-Hàn</div>
                                </div>
                            </footer>
                        </section>
                    </div>
                </div>
            </div>

            <div class="InProgressCourses">
                <div class="center">
                    <div class="Inprogress">
                        <section>
                            <header>
                                <div class="CourseHeader">
                                    <div class="header-content">Thiết kế đồ họa</div>
                                </div>
                            </header>
                            <div class="course-main-content">
                                <div class="course-main-content-center row">
                                    <div class="course-main-content-primary col-md-6 col-12">
                                        <a>
                                            <h1 class="course-main-content-title">Sử dụng Adobe cho thiết kế đồ họa</h1>
                                        </a>
                                        <span class="course-pill">Đã hoàn thành</span>
                                    </div>
                                    <div class="course-main-content-secondary col-md-6 col-12">
                                        <div class="course-main-content-button">
                                            <div class="Course-link-button">
                                                <a href="Thongtinkhoahoc.html">
                                                    <span>Đến khóa học</span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <footer class="Course-footer">
                                <div class="partner-name">
                                    <div>Đại học Bách khoa Đà Nẵng</div>
                                </div>
                            </footer>
                        </section>
                    </div>
                </div>
            </div>
        </div>
    );
};
export default Completed